import React, { Component, Fragment } from 'react'
import { Route } from 'react-router-dom'
import { v4 as uuid } from 'uuid'
import { knuthShuffle as shuffle } from 'knuth-shuffle'

import AuthenticatedRoute from './components/AuthenticatedRoute/AuthenticatedRoute'
import AutoDismissAlert from './components/AutoDismissAlert/AutoDismissAlert'
import Header from './components/Header/Header'
import SignUp from './components/SignUp/SignUp'
import SignIn from './components/SignIn/SignIn'
import SignOut from './components/SignOut/SignOut'
import ChangePassword from './components/ChangePassword/ChangePassword'
import Home from './components/Home/Home'
import PracticeOptions from './components/PracticeOptions/PracticeOptions'
import Practice from './components/Practice/Practice'
import { loadBooleanOption, saveBooleanOption } from './utils'

class App extends Component {
  constructor (props) {
    super(props)
    this.state = {
      user: null,
      msgAlerts: [],
      // all of the practice questions, shuffle them originally
      practiceQuestions: shuffle([
        {
          word: 'Rose',
          type: 'Declension', // declension or adjective
          group: '1st', // 1st declension
          genders: ['Feminine'], // some types of practice will have multiple genders (male/feminine)
          singleGender: true, // Whether this word has a single gender (across different practice options) (false for adjectives)
          audioUrl: 'sounds/01 rosa 1st decl Latin Summary of Forms (Ranieri).m4a',
          fields: [
            { case: 'Nominative', number: 'Singular', answer: 'rosa' },
            { case: 'Genitive', number: 'Singular', answer: 'rosae' },
            { case: 'Dative', number: 'Singular', answer: 'rosae' },
            { case: 'Accusative', number: 'Singular', answer: 'rosam' },
            { case: 'Ablative', number: 'Singular', answer: 'ros' },

            { case: 'Nominative', number: 'Plural', answer: 'rosae' },
            { case: 'Genitive', number: 'Plural', answer: 'rosrum' },
            { case: 'Dative', number: 'Plural', answer: 'ross' },
            { case: 'Accusative', number: 'Plural', answer: 'ross' },
            { case: 'Ablative', number: 'Plural', answer: 'ross' }
          ]
        },
        {
          word: 'Friend',
          type: 'Declension',
          group: '2nd',
          genders: ['Masculine'],
          singleGender: true,
          audioUrl: 'sounds/02a amicus 2nd decl, Latin Summary of Forms (Ranieri).m4a',
          fields: [
            { case: 'Nominative', number: 'Singular', answer: 'amcus' },
            { case: 'Genitive', number: 'Singular', answer: 'amc' },
            { case: 'Dative', number: 'Singular', answer: 'amc' },
            { case: 'Accusative', number: 'Singular', answer: 'amcum' },
            { case: 'Ablative', number: 'Singular', answer: 'amc' },
            { case: 'Vocative', number: 'Singular', answer: 'amce' },

            { case: 'Nominative', number: 'Plural', answer: 'amc' },
            { case: 'Genitive', number: 'Plural', answer: 'amcrum' },
            { case: 'Dative', number: 'Plural', answer: 'amcs' },
            { case: 'Accusative', number: 'Plural', answer: 'amcs' },
            { case: 'Ablative', number: 'Plural', answer: 'amcs' }
          ]
        },
        {
          word: 'Son',
          type: 'Declension',
          group: '2nd',
          genders: ['Masculine'],
          singleGender: true,
          audioUrl: 'sounds/02b filius 2nd decl, Latin Summary of Forms (Ranieri).m4a',
          fields: [
            { case: 'Nominative', number: 'Singular', answer: 'flius' },
            { case: 'Genitive', number: 'Singular', answer: 'fli' },
            { case: 'Dative', number: 'Singular', answer: 'fli' },
            { case: 'Accusative', number: 'Singular', answer: 'flium' },
            { case: 'Ablative', number: 'Singular', answer: 'fli' },
            { case: 'Vocative', number: 'Singular', answer: 'fli' },

            { case: 'Nominative', number: 'Plural', answer: 'fli' },
            { case: 'Genitive', number: 'Plural', answer: 'flirum' },
            { case: 'Dative', number: 'Plural', answer: 'flis' },
            { case: 'Accusative', number: 'Plural', answer: 'flis' },
            { case: 'Ablative', number: 'Plural', answer: 'flis' }
          ]
        },
        {
          word: 'Boy',
          type: 'Declension',
          group: '2nd',
          genders: ['Masculine'],
          singleGender: true,
          audioUrl: 'sounds/02c puer 2nd decl, Latin Summary of Forms (Ranieri).m4a',
          fields: [
            { case: 'Nominative', number: 'Singular', answer: 'puer' },
            { case: 'Genitive', number: 'Singular', answer: 'puer' },
            { case: 'Dative', number: 'Singular', answer: 'puer' },
            { case: 'Accusative', number: 'Singular', answer: 'puerum' },
            { case: 'Ablative', number: 'Singular', answer: 'puer' },

            { case: 'Nominative', number: 'Plural', answer: 'puer' },
            { case: 'Genitive', number: 'Plural', answer: 'puerrum' },
            { case: 'Dative', number: 'Plural', answer: 'puers' },
            { case: 'Accusative', number: 'Plural', answer: 'puers' },
            { case: 'Ablative', number: 'Plural', answer: 'puers' }
          ]
        },
        {
          word: 'Field',
          type: 'Declension',
          group: '2nd',
          genders: ['Masculine'],
          singleGender: true,
          audioUrl: 'sounds/02d ager 2nd decl, Latin Summary of Forms (Ranieri).m4a',
          fields: [
            { case: 'Nominative', number: 'Singular', answer: 'ager' },
            { case: 'Genitive', number: 'Singular', answer: 'agr' },
            { case: 'Dative', number: 'Singular', answer: 'agr' },
            { case: 'Accusative', number: 'Singular', answer: 'agrum' },
            { case: 'Ablative', number: 'Singular', answer: 'agr' },

            { case: 'Nominative', number: 'Plural', answer: 'agr' },
            { case: 'Genitive', number: 'Plural', answer: 'agrrum' },
            { case: 'Dative', number: 'Plural', answer: 'agrs' },
            { case: 'Accusative', number: 'Plural', answer: 'agrs' },
            { case: 'Ablative', number: 'Plural', answer: 'agrs' }
          ]
        },
        {
          word: 'Gift',
          type: 'Declension',
          group: '2nd',
          genders: ['Neuter'],
          singleGender: true,
          audioUrl: 'sounds/02e donum 2nd decl, Latin Summary of Forms (Ranieri).m4a',
          fields: [
            { case: 'Nominative', number: 'Singular', answer: 'dnum' },
            { case: 'Genitive', number: 'Singular', answer: 'dn' },
            { case: 'Dative', number: 'Singular', answer: 'dn' },
            { case: 'Accusative', number: 'Singular', answer: 'dnum' },
            { case: 'Ablative', number: 'Singular', answer: 'dn' },

            { case: 'Nominative', number: 'Plural', answer: 'dna' },
            { case: 'Genitive', number: 'Plural', answer: 'dnrum' },
            { case: 'Dative', number: 'Plural', answer: 'dns' },
            { case: 'Accusative', number: 'Plural', answer: 'dna' },
            { case: 'Ablative', number: 'Plural', answer: 'dns' }
          ]
        },
        {
          word: 'Long',
          type: 'Adjective Comparison', // declension or adjective
          group: 'Regular', // Regular or irregular
          audioUrl: 'some/audio/path.mp3', // TODO: a real audio path later
          fields: [
            { type: 'Positive', answer: 'longus' },
            { type: 'Comparative', answer: 'longior' },
            { type: 'Superlative', answer: 'longissimus' }
          ]
        },
        {
          word: 'One',
          type: 'Number', // declension or adjective
          audioUrl: 'some/audio/path.mp3', // TODO: a real audio path later
          fields: [
            { type: 'Cardinal', answer: 'nus (-a, -um)' },
            { type: 'Ordinal', answer: 'prmus' },
            { type: 'Adverbial', answer: 'semel' },
            { type: 'Distributive', answer: 'singul  n' }
          ]
        }
      ]),
      useMacrons: loadBooleanOption('useMacrons', true),
      // type-all
      practiceMode: localStorage.getItem('practiceMode') || 'type-all',
      // should type-one hide the other inputs or fill them in
      typeOneHideOthers: loadBooleanOption('typeOneHideOthers', true)
    }
  }

  setUseMacrons = useMacrons => {
    this.setState({ useMacrons })
    saveBooleanOption('useMacrons', useMacrons)
  }

  setPracticeMode = practiceMode => {
    this.setState({ practiceMode })
    localStorage.setItem('practiceMode', practiceMode)
  }

  setTypeOneHideOthers = typeOneHideOthers => {
    this.setState({ typeOneHideOthers })
    saveBooleanOption('typeOneHideOthers', typeOneHideOthers)
  }

  chooseRandomPracticeQuestion = questionType => {
    const randomQuestionIndex = this.state.practiceQuestions.findIndex(question => question.type === questionType)
    const randomQuestion = this.state.practiceQuestions[randomQuestionIndex]
    // copy
    const newPracticeQuestions = [...this.state.practiceQuestions]

    // remove the element
    newPracticeQuestions.splice(randomQuestionIndex, 1)

    // push it at the end of the list
    newPracticeQuestions.push(randomQuestion)

    // update the state
    this.setState({ practiceQuestions: newPracticeQuestions })

    return randomQuestion
  }

  setUser = user => this.setState({ user })

  clearUser = () => this.setState({ user: null })

  deleteAlert = (id) => {
    this.setState((state) => {
      return { msgAlerts: state.msgAlerts.filter(msg => msg.id !== id) }
    })
  }

  msgAlert = ({ heading, message, variant }) => {
    const id = uuid()
    this.setState((state) => {
      return { msgAlerts: [...state.msgAlerts, { heading, message, variant, id }] }
    })
  }

  render () {
    const { msgAlerts, user, practiceQuestions, useMacrons, practiceMode, typeOneHideOthers } = this.state

    return (
      <Fragment>
        <Header user={user} />
        {msgAlerts.map(msgAlert => (
          <AutoDismissAlert
            key={msgAlert.id}
            heading={msgAlert.heading}
            variant={msgAlert.variant}
            message={msgAlert.message}
            id={msgAlert.id}
            deleteAlert={this.deleteAlert}
          />
        ))}
        <main className="container">
          <Route exact path='/' render={() => (
            <Home msgAlert={this.msgAlert} />
          )} />
          <Route exact path='/practice-options' render={() => (
            <PracticeOptions
              msgAlert={this.msgAlert}
              useMacrons={useMacrons}
              setUseMacrons={this.setUseMacrons}
              setPracticeMode={this.setPracticeMode}
              practiceMode={practiceMode}
              typeOneHideOthers={typeOneHideOthers}
              setTypeOneHideOthers={this.setTypeOneHideOthers}
            />
          )} />
          <Route exact path='/practice' render={() => (
            <Practice
              msgAlert={this.msgAlert}
              practiceQuestions={practiceQuestions}
              chooseRandomPracticeQuestion={this.chooseRandomPracticeQuestion}
              useMacrons={useMacrons}
              practiceMode={practiceMode}
              typeOneHideOthers={typeOneHideOthers}
            />
          )} />
          <Route path='/sign-up' render={() => (
            <SignUp msgAlert={this.msgAlert} setUser={this.setUser} />
          )} />
          <Route path='/sign-in' render={() => (
            <SignIn msgAlert={this.msgAlert} setUser={this.setUser} />
          )} />
          <AuthenticatedRoute user={user} path='/sign-out' render={() => (
            <SignOut msgAlert={this.msgAlert} clearUser={this.clearUser} user={user} />
          )} />
          <AuthenticatedRoute user={user} path='/change-password' render={() => (
            <ChangePassword msgAlert={this.msgAlert} user={user} />
          )} />
        </main>
      </Fragment>
    )
  }
}

export default App



